@import url('https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap');

*{
    font-family: 'Open Sans', sans-serif;
    font-size: 16px;
    line-height: 24px;
    box-sizing: border-box;
}
.App{
    width: 30%;
    margin: 0 auto;
    padding-top: 56px;
    position: relative;
}
.white{
    height: 20vh;
    padding: 0px 30px 0px 25px;
    &__logo{
        text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
    display: flex;
    justify-content: space-between;
    position: relative;
}
.red {
    background-color: rgba(210, 51, 62, 1);
    height: 20vh;
}

.bonus{
    position: absolute;
    background-color: #fff;
    width: 80%;
    border-radius: 20px;
    box-shadow: 2px 2px 16px rgba(0, 0, 0, 0.2);
    height: 20vh;
    top: 50%;
    left: 10%;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__total{
        font-size: 24px;
        font-weight: bold;
        line-height: 32px;
    }
    &__info{
        color: #979797;
    }
    &__icon{
        img:hover{
            cursor: pointer;
        }
    }
}